import Grid from "@mui/material/Grid";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";

import ReactDiffViewer, { DiffMethod } from "react-diff-viewer";

import Classification from "./Classification";

function DisplayComparison({ datum, datumNumber, results, setResults }) {
  const oldJson = datum["pom_a"];
  const newJson = datum["pom_b"];
  const versionADate = datum["version_a_date"];
  const versionBDate = datum["version_b_date"];

  console.log({ datum });

  const newStyles = {
    variables: {
      light: {
        codeFoldGutterBackground: "#6F767E",
        codeFoldBackground: "#E2E4E5",
      },
    },
  };

  const displayOutcomes = (pom, i) => {
    return pom.map((pom) => (
      <Card key={pom.content}>
        <CardContent>
          <Typography>
            <b>{pom.label}</b> {pom.content}
          </Typography>
        </CardContent>
      </Card>
    ));
  };

  const renderFirstPosted = () => {
    return <Typography>First Posted: {datum["first_posted"]}</Typography>;
  };

  const renderStartDate = () => {
    return <Typography>Start Date: {datum["start_date"]}</Typography>;
  };
  const renderPrimaryCompletionDate = () => {
    return (
      <Typography>
        Primary Completion Date: {datum["primary_completion_date"]}
      </Typography>
    );
  };

  const renderLastUpdatePosted = () => {
    return (
      <Typography>
        Last Update Posted : {datum["last_update_posted"]}
      </Typography>
    );
  };

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        <Card>
          <CardContent>
            <Grid container>
              <Grid item xs={4}>
                <Typography variant="h6">
                  Comparison Number {datumNumber + 1} of {results.length}
                </Typography>
                <Typography variant="h4">Trial {datum["nctid"]}</Typography>
              </Grid>
              <Grid item xs={4}>
                {datum["first_posted"] ? renderFirstPosted() : null}
                {datum["start_date"] ? renderStartDate() : null}
              </Grid>

              <Grid item xs={4}>
                {datum["primary_completion_date"]
                  ? renderPrimaryCompletionDate()
                  : null}

                {datum["last_update_posted"] ? renderLastUpdatePosted() : null}
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={6}>
        <Card>
          <CardContent>
            <Typography variant="h6">
              Primary Outcomes for Version {datum["version_a"]}: {versionADate}
            </Typography>
            <Typography variant="caption">{}</Typography>
            <Stack spacing={1}>{displayOutcomes(datum["pom_a"])}</Stack>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={6}>
        <Card>
          <CardContent>
            <Typography variant="h6">
              Primary Outcomes for Version {datum["version_b"]}: {versionBDate}
            </Typography>
            <Stack spacing={1}>{displayOutcomes(datum["pom_b"])}</Stack>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        <Card>
          <CardContent>
            <Typography variant="h6">Compare the Raw Text</Typography>
            <ReactDiffViewer
              oldValue={JSON.stringify(oldJson, undefined, 4)}
              newValue={JSON.stringify(newJson, undefined, 4)}
              splitView={true}
              compareMethod={DiffMethod.WORDS}
              styles={newStyles}
              leftTitle={`Version ${datum["version_a"]}`}
              rightTitle={`Version ${datum["version_b"]}`}
              // renderContent={highlightSyntax}
            />
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
}

export default DisplayComparison;
