import * as React from "react";
import { Fragment } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";

import IconButton from "@mui/material/IconButton";
import NavigateBeforeIcon from "@mui/icons-material/NavigateBefore";
import NavigateNextIcon from "@mui/icons-material/NavigateNext";
import DownloadIcon from "@mui/icons-material/Download";

import { createTheme, ThemeProvider } from "@mui/material/styles";

import { CSVLink } from "react-csv";

const theme = createTheme({
  palette: {
    neutral: {
      main: "#fff",
      contrastText: "#000",
    },
  },
});

export default function ButtonAppBar({
  datumNumber,
  setDatumNumber,
  numComparisons,
  outputData,
  batchNum,
}) {
  const handlePrevClick = () => {
    setDatumNumber(datumNumber - 1);
  };

  const handleNextClick = () => {
    setDatumNumber(datumNumber + 1);
  };

  const renderPrevButton = () => {
    return (
      <IconButton color="inherit" variant="text" onClick={handlePrevClick}>
        <NavigateBeforeIcon></NavigateBeforeIcon>
      </IconButton>
    );
  };

  const renderNextButton = () => {
    return (
      <IconButton color="inherit" variant="text" onClick={handleNextClick}>
        <NavigateNextIcon></NavigateNextIcon>
      </IconButton>
    );
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography component="div" variant="h6" sx={{ flexGrow: 1 }}>
            Compare Trial Versions for Batch {batchNum}
          </Typography>

          <Typography component="div">
            {datumNumber > 0 ? renderPrevButton() : null}
            {datumNumber < numComparisons - 1 ? renderNextButton() : null}
          </Typography>
        </Toolbar>
      </AppBar>
    </Box>
  );
}
