import React, { useState, useEffect, Fragment } from "react";

import Container from "@mui/material/Container";
import Box from "@mui/material/Box";

import out from "./Data/out.json";

import DisplayComparison from "./Components/DisplayComparison";
import NavBar from "./Components/NavBar";

import comparisonMeanings from "./comparisonMeanings.json";

const initialResults = [];
const batchNum = out["batch_num"];
const data = out["records"];

const numComparisons = data.length;

for (let k = 0; k < numComparisons; k++) {
  initialResults.push("");
}

function App() {
  const [datumNumber, setDatumNumber] = useState(0);
  const [datum, setDatum] = useState(data[datumNumber]);
  const [results, setResults] = useState(initialResults);
  const [outputData, setOutputData] = useState([]);

  useEffect(() => {
    setDatum(data[datumNumber]);
  }, [datumNumber]);

  useEffect(() => {
    const outputData = [];
    for (let i = 0; i < numComparisons; i++) {
      const datum = {};
      datum["nctid"] = data[i]["nctid"];
      datum["version_a"] = data[i]["version_a"];
      datum["version_b"] = data[i]["version_b"];
      datum["classification_number"] = results[i];
      datum["classification_meaning"] = comparisonMeanings[results[i]];
      outputData.push(datum);
    }
    setOutputData(outputData);
  }, [results]);

  return (
    <Fragment>
      <NavBar
        datumNumber={datumNumber}
        setDatumNumber={setDatumNumber}
        numComparisons={numComparisons}
        outputData={outputData}
        batchNum={batchNum}
      ></NavBar>
      <Container>
        <Box sx={{ flexGrow: 1, m: 1 }}>
          <DisplayComparison
            datum={datum}
            datumNumber={datumNumber}
            results={results}
            setResults={setResults}
          ></DisplayComparison>
        </Box>
      </Container>
    </Fragment>
  );
}

export default App;
